<template>
    <div>
        <div class="container">
            <div class="row">
                <div class="col s12 m8 offset-m2">
                    <div class="login card-panel green white-text center">
                        <h3>New Match</h3>
                        <form>
                            <div class="input-field">                                
                                <input type="text" id="match_id" v-model="match_id">
                                <label class="white-text">Mã Trận</label>
                            </div>                            
                            <div class="input-field">                                
                                <input type="text" id="home_team" v-model="home_team">
                                <label class="white-text">Đội nhà</label>
                            </div>                            
                            <div class="input-field">                                
                                <input type="text" id="away_team" v-model="away_team">
                                <label class="white-text">Đội khách</label>
                            </div>                            
                            <div class="input-field">                                
                                <input type="text" id="home_flag" v-model="home_flag">
                                <label class="white-text">Cờ Đội nhà</label>
                            </div>                            
                            <div class="input-field">                                
                                <input type="text" id="away_flag" v-model="away_flag">
                                <label class="white-text">Cờ Đội khách</label>
                            </div>                            
                            <div class="input-field">                                
                                <input type="text" id="start_time" v-model="start_time">
                                <label class="white-text">Thời gian</label>
                            </div>                            
                            <div class="input-field">                                
                                <input type="text" id="start_date" v-model="start_date">
                                <label class="white-text">Ngày thi đấu</label>
                            </div>                           
                            
                            <button v-on:click="createNew" class="btn btn-large grey lighten-4 black-text">CREATE</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
import db from './firebaseInit'

export default {
    name: 'login',
    data: function() {
        return {
            match_status: 'not_start',
            match_id: '',
            home_team: '',
            home_score: '0',
            home_flag: '',
            away_team: '',
            away_score: '0',
            away_flag: '',
            start_time: '',
            start_date: '',
        };
    },
    methods: {
        createNew: function(e) {
            db.collection('matchs').add({
                match_id: this.match_id,
                match_status: 'not_start',
                home_team: this.home_team,
                home_score: '0',
                home_flag: this.home_flag,
                away_team: this.away_team,
                away_flag: this.away_flag,
                away_score: '0',
                start_time: this.start_time,
                start_date: this.start_date
            }).then(docRef => {
                this.$router.push('/');
            }).catch(err => {
                console.log(err);
            });

            e.preventDefault();
        }
    }
}
</script>



